/* NORMALIZE & RESET */
:root {
  --indigo: #3d14b8;
  --salmon: #f66e55;
  --teal: #12a5a5;
  --persian: #031330;
  --success: #14b866;
  --danger: #dc1829;
  --warning: #e2b912;
  --info: #1481b8;
  --gray-100: #c9c7d1;
  --gray-200: #abb0ba;
  --gray-300: #5c6670;
  --gray-400: #312e38;
  --theme-light: #f5f5f5;
  --theme-dark: #16161d;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 62.5%;
  font-weight: 300;
  box-sizing: border-box;
}

*::after,
*::before,
* {
  box-sizing: border-box;
}

body {
  font-size: 1.6rem;
  padding: 1.8rem 2rem;
  background-color: var(--theme-light);
  color: var(--theme-dark);
  min-height: 100vh;
}

/* UTILITIES CLASSES  */
.d-inline {
  display: inline;
}

.d-inblock {
  display: inline-block;
}

.d-block {
  display: block;
}

.d-flex {
  display: flex;
}

.d-inflex {
  display: inline-flex;
}

.d-grid {
  display: grid;
}

.d-ingrid {
  display: inline-grid;
}

.d-none {
  display: none;
}

.d-table {
  display: table;
}